import { Component, OnInit } from '@angular/core';
import { SelectMultipleControlValueAccessor } from '@angular/forms';
import { EmployeeModel } from 'src/app/model/employeemodel';
import { EmployeeService } from '../../../service/employee.service'

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

  dtOptionsTblEmployee: DataTables.Settings = {}
  listEmployees: EmployeeModel[] = []

  alert: any = {
    status: false,
    type: "warning",
    message: ""
  }

  editEmployee() {
    this.alert.type = "warning"
    this.alert.message = "You have editted employee"
    this.alert.status = true
    setTimeout(() => this.alert.status = false, 5000)
  }

  deleteEmployee() {
    this.alert.type = "danger"
    this.alert.message = "You have deleted employee"
    this.alert.status = true
    setTimeout(() => this.alert.status = false, 5000)
  }

  constructor(
    private employeeService: EmployeeService
  ) { }

  getEmployee() {
    this.listEmployees = this.employeeService.getEmployees()
  }

  retrieveEmployee() {

  }

  ngOnInit(): void {
    this.getEmployee();

    this.dtOptionsTblEmployee = {
      pagingType: "full_numbers",
      pageLength: 10,
      ordering: true,
      lengthMenu: [[10, 25, 50, 100, -1], [10, 25, 50, 100, 'All']],
      columnDefs: [
        {
          targets: [8, 9],
          orderable: false
        },
        {
          targets: [8, 9],
          searchable: false
        }
      ],
    }
  }

}

