import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { EmployeeModel } from 'src/app/model/employeemodel';
import { EmployeeService } from 'src/app/service/employee.service';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss']
})
export class CreateComponent implements OnInit {

  employeeModel: EmployeeModel = EmployeeModel.defaultData()

  constructor(
    private employeeService: EmployeeService,
    private router: Router
  ) { }

  submit(form: NgForm) {
    if (form.valid) {
      let data = this.employeeModel
      data.birthDate = form.value.birthDate.year + '-' + form.value.birthDate.month + "-" + form.value.birthDate.day
      this.employeeService.addEmployee(data)
      this.router.navigate(['/employee']);
    }
  }

  ngOnInit(): void {
  }

}
