export class EmployeeModel {
    firstName!: String
    lastName!: String
    username!: String
    email!: String
    birthDate!: String
    basicSalary!: Number
    status!: String
    group!: String
    description!: String
    password!: String

    static defaultData() {
        return {
            firstName: '',
            lastName: '',
            username: '',
            email: '',
            birthDate: '',
            basicSalary: 0,
            status: '',
            group: '',
            description: new Date().toISOString().split('T')[0],
            password: '123456'
        }
    }
}